import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, isEmpty, onlyNumber, WindowComponent } from 'summer';
import { CentroCusto, CLASS_NAME } from '../../../../models/dtos/ger/centroCusto';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

interface CentroCustoEditViewProps extends EditViewProps<CentroCusto> {}

const CentroCustoEditView: FC<CentroCustoEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const { currentState, globalParameter } = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const formProps = useForm<CentroCusto>({ defaultValues: selectedItem, mode: 'onBlur' });

  useEffect(
    () => {
      setTimeout(() => {
        formProps.register({ name: 'grupoCentroCusto' });
        if (!selectedItem || isEmpty(selectedItem.id)) {
          formProps.setValue('grupoCentroCusto', globalParameter.grupoEmpresaConfig.grupoCentroCusto);
        }
      }, 700);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const tamanhoCodigo = currentState === 'MCG' ? globalParameter.grupoEmpresaConfig.grupoCentroCusto.nroDigitosCentroCusto : 18;

  return (
    <WindowComponent title="Manutenção de Centros de Custo" wikiId={4415} useGlobalParameter={true} globalParameter={globalParameter}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { ...onlyNumber(0, 9) } }}
              isPrependLabel={true}
              placeholder="Preenchimento Opcional - Codificação Automática"
              id="codigoReduzido"
              label="Código Reduzido"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              isPrependLabel={true}
              formProps={{
                ...formProps,
                validation: { ...onlyNumber(0, tamanhoCodigo) },
              }}
              placeholder="Preenchimento Opcional - Codificação Automática"
              id="codigo"
              label="Código"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 40, required: true } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <CustomInput type="switch" label="Analítica" name="analitica" id="checkAnalitica" innerRef={formProps.register} />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default CentroCustoEditView;
